import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Me from './pages/Me';
import Projects from './pages/Projects';
import Work from './pages/Work';
import Education from './pages/Education';

function App() {
  const [user, setUser] = useState(null);
  useEffect(() => {
    // NOTE: Use your username below
    fetch('https://gitconnected.com/v1/portfolio/khaminvy')
      .then(res => res.json())
      .then(user => {
        setUser(user);
      });
  }, []);

  if (!user) {
    return <div />;
  }

  return (
    <Router>
      <Switch>
      <Route exact path="/" render={() =>  <Me user={user} /> } />
        <Route path="/projects" render={() => <Projects user={user} />} />
        <Route path="/work" render={() => <Work user={user} />} />
        <Route path="/education" render={() => <Education user={user} />} />
      </Switch>
    </Router>
  )
}

export default App;
